import React, { useState, useEffect } from 'react'
import './dashboard.css';
import firebase from 'firebase/app';
import 'firebase/auth';
import { useHistory, Link } from 'react-router-dom';


function Dashboard() {

    const [subjectName, SetsubjectName] = useState('');
    const [subjectTeacher, SetsubjectTeacher] = useState('');
    const [SubjectList, SetsubjectList] = useState([]);

    const history = useHistory();
    const db = firebase.firestore();

    useEffect(() => {
        console.log('component loaded');
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then((doc) => {
            if (doc.exists) {
                SetsubjectList(doc.data().Subject);

            } else {
                console.log('No Data is Present');
            }

        }).catch((error) => {
            console.log(error.message, error.code);
        })
    }, []);



    const handleSubmit = (e) => {
        console.log(subjectName, subjectTeacher);
        // db.collection('users').doc()  
        db.collection('users').doc(firebase.auth().currentUser.uid).collection(subjectName).add({
            Subject_Name: subjectName,
            Subject_Teacher: subjectTeacher
        }).then(() => {
            console.log('Class created successfully')

        }).catch((error) => {
            console.log(error.message, error.code);
        });

        db.collection('users').doc(firebase.auth().currentUser.uid).update({
            Subject: [...SubjectList, subjectName],
        }).then(() => {
            console.log('document updated successfully');
            SetsubjectList([...SubjectList, subjectName]);
        }).catch((error) => {
            console.log(error.message, error.code);
        })
    }

    return (
        <div>
            <div>
                {firebase.auth().currentUser !== null ? (
                    <div className="box1">
                        <div className="classes">
                            <h2>My Classes <img   style={{height:28, }} src="https://img.icons8.com/ios-filled/25/000000/class.png"/></h2>
                            <hr />
                            <div className="card_display">
                                {
                                    SubjectList.map((element) => {
                                        return (
                                            <Link to={`/${element}`} className="card_element">
                                                <div  >
                                                    <h3>{element}<img style={{height:18,marginLeft:5,marginTop:3 }} src="https://img.icons8.com/ios-filled/50/000000/long-arrow-right.png"/></h3>
                                                    <hr />
                                                    Click Here to take attendence and View attendences
                                                </div>
                                            </Link>
                                        )
                                    })
                                }
                            </div>
                        </div>
                        <div className="new_classes">
                            <h3>Add Class <img   style={{height:18, }} src="https://img.icons8.com/ios-filled/25/000000/class.png"/></h3>
                            <label htmlFor="subject_name">Subject Name</label>
                            <input type="text" placeholder="Enter the name of subject" name="subject_name" onChange={(e) => SetsubjectName(e.target.value)} />

                            <label htmlFor="teacher_name">Teacher Name</label>
                            <input type="text" placeholder="Enter the name of subject teacher" name="teacher_name" onChange={(e) => SetsubjectTeacher(e.target.value)} />

                            <button onClick={handleSubmit}><img style={{height:14, }} src="https://img.icons8.com/ios/20/000000/plus--v1.png"/> Create Class</button>

                        </div>

                    </div>
                ) : (
                    <div>
                        {history.push('/')}
                    </div>
                )}
            </div>
        </div>
    )

}

export default Dashboard
