import React,{useState} from 'react'
import './SignUp.css'
import firebase from 'firebase/app';
import 'firebase/auth';
import {useHistory} from 'react-router-dom';

function SignUp() {

    const [firstName, SetfirstName]=useState('');
    const [lastName, SetlastName]=useState('');
    const [email, Setemail]=useState('');
    const [password, Setpassword]=useState('');
    const history=useHistory();

    const handleSignUp=(e)=>
    {
        e.preventDefault();
        firebase.auth().createUserWithEmailAndPassword(email, password).then((userCredential)=>{

                console.log(userCredential.user.uid);
               firebase.firestore().collection('users').doc(userCredential.user.uid).set({
                   FirstName:firstName,
                   LastName:lastName,
                   EMail:email,
                   Password:password,
                   Subject:[],
                   Student_List:[],
               }).then(()=>{
                   console.log("Document written with ID: ");
                   history.push('/dashboard');
               })

        }).catch((error)=>{
            console.log(error.message, error.code);
        })
    }

    return (
        <div className="box">

        <div className="first_name">
            <label  htmlFor="fname">First Name</label>
            <input type="text" placeholder="Enter Your First Name" name="fname"  onChange={(e)=>SetfirstName(e.target.value)}/>
        </div>

        <div className="last_name">
            <label  htmlFor="lname">Last Name</label>
            <input type="text" placeholder="Enter Your Last Name" name="lname" onChange={(e)=>SetlastName(e.target.value)} />
        </div>

        <div className="email">
            <label htmlFor="email">E-Mail</label>
            <input type="email" placeholder="Enter Your Email" name="email" onChange={(e)=>Setemail(e.target.value)} />
        </div>

        <div className="password">
            <label  htmlFor="password">Password</label>
            <input type="password" placeholder="Enter Your Password" name="password" onChange={(e)=>Setpassword(e.target.value)} />
        </div>

        <button onClick={handleSignUp}>Sign Up</button>

    </div>
    )
}

export default SignUp
