import React, { useState } from 'react'
import './AttendanceViewer.css';
import { useParams } from 'react-router-dom';
import firebase from 'firebase/app';
import 'firebase/auth';

function AttendanceViewer() {

    let { id } = useParams();
    const [date, SetDate] = useState('');
    const [st_pre, Setst_pre]=useState([]);
    const [st_abs, Setst_abs]=useState([]);
    const db = firebase.firestore();

    const handleSubmit=(e)=>{
        e.preventDefault();
        db.collection('users').doc(firebase.auth().currentUser.uid).collection(id).doc(date).get().then((docRef)=>{
            console.log(docRef.data());
            Setst_pre(docRef.data().Students_Present);
            Setst_abs(docRef.data().Students_Absent);
        }).catch((error)=>{
            console.log(error.message, error.code);
        })
        
    }

    return (
        <div className="viewer_outer">
            <h1 className="t"><img src="https://img.icons8.com/ios-filled/25/000000/search--v1.png"/>  View Your Attendance</h1>
            <div className="pckr">
                <label htmlFor="at_date">Choose Date :</label>
                <input type="date" name="at_date" id="date_pckr" onChange={(e) => { SetDate(e.target.value) }} />
                <button className="view_ata" onClick={handleSubmit}><img src="https://img.icons8.com/ios-filled/14/000000/date-to.png"/> View Attendence</button>
            </div>

            <div className="viewer">
            <div className="stu_lis2">
                        <h3 className="table_header1  "><img src="https://img.icons8.com/ios/20/000000/attendance-mark.png"/>  Students Present</h3>
                        {(st_pre.length!==0) && st_pre.map((element) => <div className="student_names pree" >{element}</div>)}
                    </div>

                    <div className="stu_lis2">
                        <h3 className="table_header1"><img src="https://img.icons8.com/ios/20/000000/attendance-mark.png"/>  Students Absent</h3>
                        { (st_abs.length!==0) && st_abs.map((element) => <div className="student_names absee" >{element}</div>)}
                    </div>
            </div>

        </div>
    )
}

export default AttendanceViewer
