import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import './Checker.css';
import firebase from 'firebase/app';
import 'firebase/auth';
import {Link} from 'react-router-dom';

function Checker() {
    let { id } = useParams();
    const [date, SetDate] = useState('');
    const [student_name, Setstudent_name] = useState('');
    const [student_regno, Setstudent_regno] = useState('');
    const [st_pre, Setst_pre]=useState([]);
    const [st_abs, Setst_abs]=useState([]);
    const [st_lis, Setst_lis] = useState([]);
    const db = firebase.firestore();
    const [message, SetMessage]=useState('');
    const [message1, SetMessage1]=useState('');

    useEffect(() => {
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then((doc) => {
            if (doc.exists) {
                Setst_lis(doc.data().Student_List);
                console.log(st_lis);
            } else {
                console.log('No Data is Present');
            }

        }).catch((error) => {
            console.log(error.message, error.code);
        })
    }, []);

    const handleSubmit = (e) => {
        e.preventDefault();
        db.collection('users').doc(firebase.auth().currentUser.uid).collection(id).doc(date).set({
            Students_Present:st_pre,
            Students_Absent:st_abs,
        }).then(()=>{
            console.log('Attendence Taken Successfully');
            Setst_abs([]);
            Setst_pre([]);
            SetDate('');
            SetMessage('Attendence taken successfully');
        }).catch((error)=>{
            console.log(error.message, error.code);
        })

    }

    const handleSubmit1 = (e) => {
        e.preventDefault();
        let st = student_name + "-" + student_regno;
        db.collection('users').doc(firebase.auth().currentUser.uid).update({
            Student_List: [...st_lis, st],
        }).then(() => {
            console.log('document updated successfully');
            Setst_lis([...st_lis, st]);
            SetMessage1('Student Added Successfully');
        }).catch((error) => {
            console.log(error.message, error.code);
        })

    }

    return (
        <div className="checker_outer">
            <div className="tabular">
                <h1>{id} <img src="https://img.icons8.com/material-outlined/28/000000/book.png"/></h1>
                <hr />
                <label htmlFor="at_date">Choose Date :</label>
                <input type="date" name="at_date" id="date_pckr" onChange={(e) => { SetDate(e.target.value) }} />
                <button onClick={handleSubmit}><img src="https://img.icons8.com/ios-filled/14/000000/date-to.png"/> Take Attendence</button>
                { message.length!==0 && <span onClick={()=>{SetMessage(''); console.log('clicked');}}>{message}</span>}
                <Link to={`/view_attendance/${id}`}>
                <button className="view_at"><img src="https://img.icons8.com/material-rounded/14/000000/view-file.png"/>  View Attendence</button>
                </Link>
                <br />
                <section className="ot_st_lis">
                    <div className="stu_lis1">
                        <h3 className="table_header"><img src="https://img.icons8.com/ios/20/000000/student-registration.png"/> Students List</h3>
                        {st_lis.map((element, index) => <div className="student_names" >
                            {index+1+".) "+element.split('-')[0]}{ /*console.log({element})*/}  
                            <button className="P" onClick={()=>{Setst_pre([...st_pre,element])}}>Present</button> 
                            <button className="A"  onClick={()=>Setst_abs([...st_abs,element])}>Absent</button>
                            </div>
                            )}
                    </div>

                    <div className="stu_lis">
                        <h3 className="table_header"><img src="https://img.icons8.com/ios/20/000000/attendance-mark.png"/> Students Present</h3>
                        {(st_pre.length!==0) && st_pre.map((element) => <div className="student_names pree" >{element}</div>)}
                    </div>

                    <div className="stu_lis">
                        <h3 className="table_header"><img src="https://img.icons8.com/ios/20/000000/attendance-mark.png"/> Students Absent</h3>
                        { (st_abs.length!==0) && st_abs.map((element) => <div className="student_names absee" >{element}</div>)}
                    </div>
                </section>
            </div>

            <div className="add_students">
                <h5>
                <img src="https://img.icons8.com/dotty/20/000000/user.png"/>Add Students
                </h5>
                <label htmlFor="student_name">Student Name</label>
                <input type="text" placeholder="Enter the name of student" name="student_name" onChange={(e) => Setstudent_name(e.target.value)} />

                <label htmlFor="regno">Reg No of Student</label>
                <input type="text" placeholder="Enter the Reginteration no of Student" name="regno" onChange={(e) => Setstudent_regno(e.target.value)} />

                <button onClick={handleSubmit1}><img src="https://img.icons8.com/material-outlined/16/000000/plus--v1.png"/> Add Student</button>
                <h5>{message1}</h5>

            </div>
        </div>
    )
}

export default Checker
