import React from 'react'
import './Navbar.css';
import firebase from 'firebase/app';
import 'firebase/auth';
import { IfFirebaseAuthed , IfFirebaseUnAuthed} from '@react-firebase/auth';

import { Link } from 'react-router-dom';

function Navbar() {
 
    return (
        <div className="outer">
            <div className="logo">
               <img src="https://img.icons8.com/dotty/48/000000/manager.png"/>
               <div>Attendence Manager</div>
            </div>

            <div className="login_options">

             <IfFirebaseUnAuthed>
                 <div className="home">
                   <Link to="/" className="a"><img src="https://img.icons8.com/ios-filled/18/000000/home.png"/> Home</Link> 
                </div>
                <div className="signup">
                   <Link to="/signup" className="a"><img src="https://img.icons8.com/ios-filled/18/000000/add-user-male.png"/>  Sign Up</Link> 
                </div>
                <div className="login" >
                    <Link to="/login" className="a"><img src="https://img.icons8.com/ios-filled/18/000000/login-rounded-right.png"/> Log In</Link>
                </div>
                </IfFirebaseUnAuthed>

                <IfFirebaseAuthed>
                 <div className="dashboard">
                   <Link to="/dashboard" className="a"> <img src="https://img.icons8.com/fluent-systems-filled/18/000000/dashboard-layout.png"/> My Dashboard </Link> 
                  
                 </div>

                    <div className="logout" onClick={() => firebase.auth().signOut()}>
                    <Link to="/" className="a"><img src="https://img.icons8.com/ios-filled/18/000000/logout-rounded-up.png"/>  Log out</Link> 
                    </div>
                </IfFirebaseAuthed>

            </div>
        </div>
    )
}

export default Navbar;
